
.v-button-group {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  font-size: 0;
}
.v-button-group .v-button {
  position: relative;
}
.v-button-group .v-button:hover {
  z-index: 1;
}
.v-button-group .v-button:not(:first-of-type):not(:last-of-type) {
  border-radius: 0;
}
.v-button-group.horizontal .v-button:first-of-type {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.v-button-group.horizontal .v-button:last-of-type {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.v-button-group.horizontal .v-button + .v-button {
  margin-left: -1px;
}
.v-button-group.vertical .v-button {
  display: block;
}
.v-button-group.vertical .v-button:first-of-type {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.v-button-group.vertical .v-button:last-of-type {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
.v-button-group.vertical .v-button + .v-button {
  margin-top: -1px;
}



