
.v-button {
  font-size: 12px;
  color: rgba(0, 0, 0, 0.5);
  display: inline-block;
  line-height: 1.5;
  border: 1px solid transparent;
  white-space: nowrap;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-radius: 4px;
  background-color: #f7f7f7;
  border-color: #dddee1;
  text-decoration: none !important;
  transition: border-color 200ms, color 200ms, background-color 200ms;
}
.v-button [class^="icon-"] + *,
.v-button [class*=" icon-"] + * {
  margin-left: 0.33333333em;
}
.v-button .v-button-loading {
  margin-right: 0.33333333em;
}
.v-button.icon .v-button-loading {
  margin-right: 0;
}
.v-button > * {
  display: inline-block;
  vertical-align: top;
}
.v-button.large {
  padding: 0.5em 1.25em;
}
.v-button.large.icon {
  padding: 0.5em;
}
.v-button.large.icon.text {
  padding: 0;
}
.v-button.default {
  padding: 0.5em 1.25em;
}
.v-button.default.icon {
  padding: 0.5em;
}
.v-button.default.icon.text {
  padding: 0;
}
.v-button.small {
  padding: 0.16666667em 0.58333333em;
}
.v-button.small.icon {
  padding: 0.16666667em;
}
.v-button.small.icon.text {
  padding: 0;
}
.v-button.icon > * {
  line-height: 1;
  font-size: 1.5em;
}
.v-button.circle {
  border-radius: 1.5em;
}
.v-button:hover {
  color: #2a71e3;
  border-color: #2a71e3;
  background-color: #ffffff;
}
.v-button.ghost {
  background-color: transparent;
}
.v-button.text,
.v-button.min-icon {
  border-color: transparent;
  background-color: transparent;
}
.v-button.active,
.v-button:active {
  color: #164faa;
}
.v-button.loading {
  cursor: default;
  pointer-events: none;
  opacity: 0.6;
}
.v-button.primary {
  color: #fff;
  border-color: #2a71e3;
  background-color: #2a71e3;
}
.v-button.primary:hover {
  border-color: #4180e6;
  background-color: #4180e6;
}
.v-button.primary:active,
.v-button.primary.active {
  border-color: #1c64d7;
  background-color: #1c64d7;
}
.v-button.success {
  color: #fff;
  border-color: #0eec38;
  background-color: #0eec38;
}
.v-button.success:hover {
  border-color: #22f249;
  background-color: #22f249;
}
.v-button.success:active,
.v-button.success.active {
  border-color: #0dd432;
  background-color: #0dd432;
}
.v-button.info {
  color: #fff;
  border-color: #00B4FF;
  background-color: #00B4FF;
}
.v-button.info:hover {
  border-color: #1abcff;
  background-color: #1abcff;
}
.v-button.info:active,
.v-button.info.active {
  border-color: #00a2e6;
  background-color: #00a2e6;
}
.v-button.warning {
  color: #fff;
  border-color: #ffb300;
  background-color: #ffb300;
}
.v-button.warning:hover {
  border-color: #ffbb1a;
  background-color: #ffbb1a;
}
.v-button.warning:active,
.v-button.warning.active {
  border-color: #e6a100;
  background-color: #e6a100;
}
.v-button.error {
  color: #fff;
  border-color: #dd4b39;
  background-color: #dd4b39;
}
.v-button.error:hover {
  border-color: #e15f4f;
  background-color: #e15f4f;
}
.v-button.error:active,
.v-button.error.active {
  border-color: #d73925;
  background-color: #d73925;
}
.v-button[disabled]:not(.text),
.v-button.disabled:not(.text) {
  cursor: not-allowed;
  pointer-events: none;
  color: #bbbec4 !important;
  background-color: #f7f7f7 !important;
  border-color: #dddee1 !important;
}
.v-button.text[disabled],
.v-button .text.disabled {
  cursor: not-allowed;
  pointer-events: none;
  color: #bbbec4 !important;
}
.v-button-loading-icon {
  vertical-align: top;
  -webkit-animation: loading 1.2s linear infinite;
          animation: loading 1.2s linear infinite;
}
@-webkit-keyframes loading {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes loading {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}



