
.vue-message-container {
  position: fixed;
  z-index: 299;
  top: 24px;
  left: 50%;
  -webkit-transform: translate(-50%, -100%);
          transform: translate(-50%, -100%);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0.4em 1em 0.4em 0.6em;
  background-color: #fff;
  border-radius: 0.2em;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
  visibility: hidden;
  opacity: 0;
  color: rgba(0, 0, 0, 0.5);
  transition: opacity linear 200ms, visibility linear 200ms, -webkit-transform linear 200ms;
  transition: transform linear 200ms, opacity linear 200ms, visibility linear 200ms;
  transition: transform linear 200ms, opacity linear 200ms, visibility linear 200ms, -webkit-transform linear 200ms;
}
.vue-message-container.with-close {
  padding-right: 0.6em;
}
.vue-message-container.show {
  visibility: visible;
  opacity: 1;
  -webkit-transform: translate(-50%, 0%);
          transform: translate(-50%, 0%);
}
.vue-message-close-btn {
  line-height: 1em;
  height: 1em;
  color: inherit;
  opacity: 0.4;
  display: none;
  margin-left: 0.6em;
}
.vue-message-close-btn.show {
  display: block;
}
.vue-message-close-btn:hover {
  opacity: 0.7;
}
.vue-message-type-icon {
  margin-right: 0.4em;
}
.message-info {
  color: #00B4FF;
}
.message-success {
  color: #0eec38;
}
.message-warning {
  color: #ffb300;
}
.message-error {
  color: #dd4b39;
}
.message-loading {
  color: #2a71e3;
  opacity: 0.7;
  font-size: 0.8em;
  margin-right: 0.6em;
}
.loading-animate {
  -webkit-animation: loading 1.2s linear infinite;
          animation: loading 1.2s linear infinite;
}
@-webkit-keyframes loading {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes loading {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}



