
.v-radio-group {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  font-size: 0;
}
.v-radio-group .v-radio-container.button {
  position: relative;
}
.v-radio-group .v-radio-container.button.picked {
  z-index: 1;
}
.v-radio-group .v-radio-container.button:not(:first-of-type):not(:last-of-type) .v-radio-fake-border {
  border-radius: 0;
}
.v-radio-group.horizontal .v-radio-container.button:first-of-type .v-radio-fake-border {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.v-radio-group.horizontal .v-radio-container.button:last-of-type .v-radio-fake-border {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.v-radio-group.horizontal .v-radio-container.button + .v-radio-container.button {
  margin-left: -1px;
}
.v-radio-group.vertical .v-radio-container {
  display: block;
}
.v-radio-group.vertical .v-radio-container:first-of-type.button .v-radio-fake-border {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.v-radio-group.vertical .v-radio-container:last-of-type.button .v-radio-fake-border {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
.v-radio-group.vertical .v-radio-container.button + .v-radio-container.button {
  margin-top: -1px;
}



