
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgba(0, 0, 0, 0.5);
  height: 100%;
}
html,
body {
  background-color: #fff;
  height: 100%;
  overflow: hidden;
}
blockquote {
  margin: 0;
}
a {
  color: #42b983;
}
.shadow-transition {
  transition: box-shadow linear 200ms;
}
.page-header-container {
  width: 100%;
  top: 0;
  left: 0;
  position: fixed;
}
.page-body-container {
  box-sizing: border-box;
  height: 100%;
  padding-top: 60px;
}
.content-view {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: stretch;
      -ms-flex-align: stretch;
          align-items: stretch;
}
.content-list {
  width: 200px;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  padding: 14px 0;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.25);
  overflow-x: hidden;
  overflow-y: auto;
}
.content-list::-webkit-scrollbar {
  width: 0px;
}
.content-list-group-title {
  cursor: default;
  opacity: 0.7;
  padding: 7px 24px;
}
.content-list-item {
  font-size: 14px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-decoration: none;
  padding: 14px 24px;
  color: rgba(0, 0, 0, 0.5);
  transition: background-color 200ms, border-color 200ms, color 200ms;
  border-right: 2px solid transparent;
  box-sizing: border-box;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
}
.content-list-item:hover {
  background-color: rgba(0, 0, 0, 0.05);
}
.content-list-item.router-link-active {
  color: #42b983;
  border-right-color: #42b983;
}
.content-list-item-icon {
  font-size: 0.8em;
  margin-right: 0.5em;
}
.content-body {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}
.content-body-inner {
  padding: 5px 40px;
}



